.btn-slider::before {
    display: inline-block;
    font-size: 1.125rem;
    color: white;
    content: "\f178";
    font-family: "Font Awesome 5 Pro";
    vertical-align: middle;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    position: absolute;
    opacity: 0;
    right: 30px;
    -moz-transition: right 400ms cubic-bezier(0.33, 0.005, 0.125, 1.005) 100ms, color 0s linear, opacity 400ms cubic-bezier(0.33, 0.005, 0.125, 1.005);
    transition: right 400ms cubic-bezier(0.33, 0.005, 0.125, 1.005) 100ms, color 0s linear, opacity 400ms cubic-bezier(0.33, 0.005, 0.125, 1.005);
}

.btn-slider:hover:before {
    right: 30px;
    opacity: 1;
    color: white;
    -moz-transition: right 400ms cubic-bezier(0.33, 0.005, 0.125, 1.005) 0ms, color 0s linear, opacity 400ms cubic-bezier(0.33, 0.005, 0.125, 1.005) 300ms;
    transition: right 400ms cubic-bezier(0.33, 0.005, 0.125, 1.005) 0ms, color 0s linear, opacity 400ms cubic-bezier(0.33, 0.005, 0.125, 1.005) 300ms;
}

