/* - - - - - - - - - - - - - - - - - - - -
 Vertically aligns text inside col - (AS - 07/09/22)
- - - - - - - - - - - - - - - - - - - - */
.flexy-text {
    display: flex;
    flex-direction: column;
}

.flexy-text .flexy-content {
    margin: auto 0;
    padding: 0;
}

.style-guide-CodeExample {
    display: none;
}

.style-guide-Example {
    margin-top: 20px;
}

body {
    font-family: "Open Sans", sans-serif;
    color: #1D1D1D;
    background: #F7F8FD;
}

.bg-dark {
    background: #F7F8FD;
}

.wrapper {
    min-height: 100vh;
    margin-bottom: -240px;
    padding-bottom: 240px;
}

.bg-grey {
    background: #F7F8FD;
}

.dropdown .dropdown-toggle:after {
    content: "\f078";
    font-family: "Font Awesome 5 Pro";
    margin-right: 10px;
    font-weight: 600;
    font-size: 10px;
    border: none;
    vertical-align: 0;
}

.dropdown.show .dropdown-toggle:after {
    content: "\f077";
}

@media (min-width: 576px) {
    .wrapper {
        min-height: 100vh;
        margin-bottom: -206px;
        padding-bottom: 206px;
    }
}

.turasSection {
    padding-top: 3rem;
    padding-bottom: 3rem;
}

.scroll-to-top {
    cursor: pointer;
    height: 54px;
    width: 54px;
    background: #0556AD;
    position: fixed;
    bottom: 100px;
    right: 30px;
    line-height: 50px;
    text-align: center;
    display: none;
    z-index: 1000;
    border-radius: 4px;
    border: 2px solid #eff2f6;
}

.scroll-to-top .fa-arrow-up {
    color: #FFF;
}

@media (min-width: 768px) {
    .wrapper {
        min-height: 100vh;
        margin-bottom: -214px;
        padding-bottom: 214px;
    }

    #universal-nav ul #app-dropdown {
        display: inline-block;
    }

    #universal-nav ul li#app-dash a:before {
        display: none;
    }

    footer ul {
        text-align: left;
    }

    footer ul li {
        display: inline-block;
        margin-right: 15px;
        font-size: 1rem;
    }

    footer ul li.helpdesk {
        display: inline-block;
    }

    footer ul li.helpdesk a {
        margin: 0px;
    }

    footer img {
        width: 80px;
        float: right;
        display: block;
    }

    footer img.turasLogo {
        width: 110px;
        float: left;
        margin-bottom: 10px;
    }
}

@media only screen and (max-width: 768px) {
    .breadcrumb {
        overflow-x: scroll;
        overflow-y: hidden;
        white-space: nowrap;
        display: inline-block;
        width: 100%;
    }

    li.breadcrumb-item {
        display: inline;
    }

    .breadcrumb-item + .breadcrumb-item::before {
        float: none;
    }
}

@media (min-width: 1200px) {
    /* - - - - - - - - - - - - - - - - - - - -
                Main navigation
    - - - - - - - - - - - - - - - - - - - - */
    #main-nav .container {
        max-width: 1140px;
        margin: auto;
    }

    #main-nav.navbar.bg-dark {
        padding: 0;
    }

    #main-nav.navbar.bg-dark .menu-text {
        display: none;
    }

    #main-nav.navbar .nav-item .nav-link {
        color: #FFF;
        padding: 19px 12px;
        font-size: 1rem;
    }

    #main-nav.navbar .nav-item .nav-link:hover {
        background: #044A96;
    }

    #main-nav.navbar .nav-item.active {
        background: #044A96;
        font-weight: 600;
    }

    #main-nav.navbar .nav-item.dropdown.show {
        background: #044A96;
    }

    #main-nav.navbar .nav-item.dropdown .dropdown-toggle:after {
        vertical-align: 2px;
    }

    #main-nav.navbar .nav-item.dropdown .dropdown-menu .dropdown-item {
        font-size: 1rem;
    }

    #main-nav.navbar .nav-item.dropdown .dropdown-menu .dropdown-item:hover {
        color: #FFF;
        background: #0556AD;
    }

    #main-nav.navbar .nav-item.dropdown .dropdown-menu .dropdown-item:active {
        background: #0556AD;
    }

    #main-nav.navbar .nav-item.dropdown .dropdown-menu .dropdown-divider {
        margin: 0;
    }
}
