#leftnav {
    position: sticky;
    top: 2.5rem;
}

#leftnav ul a {
    cursor: pointer;
}

#leftnav ul li:hover {
    text-decoration: none;
}

#leftnav ul li a:hover {
    text-decoration: underline;
}

#leftnav .panel-body {
    overflow-y: auto;
    max-height: 80vh;

    --mask-bottom: linear-gradient(to bottom,
    rgba(0, 0, 0, 1) 0, rgba(0, 0, 0, 1) 90%, rgba(0, 0, 0, 0) 97%, rgba(0, 0, 0, 0) 0
    ) 100% 50% / 100% 100% repeat-x;

    -webkit-mask: var(--mask-bottom);
    mask: var(--mask-bottom);
}

#leftnav ul ul li {
    padding-top: 0.35em;
    padding-bottom: 0.35em;
}
